import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PictureSourceType } from '@ionic-native/camera/ngx';
import { ActionSheetController, AlertController } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { MONTHS_SHORT } from 'src/app/commons/locale';
import { alertCommunicationError } from 'src/app/commons/user-feedback';
import { ActoCondicionInsegura } from 'src/app/entities/acto-condicion-insegura';
import { Entity } from 'src/app/entities/entity';
import { Proceso } from 'src/app/entities/proceso';
import { Sede } from 'src/app/entities/sede';
import { Tipo } from 'src/app/entities/tipo';
import { ActoCondicionInseguraService } from 'src/app/services/acto-condicion-insegura.service';
import { CameraService } from 'src/app/services/camera.service';
import { ContextService } from 'src/app/services/context.service';
import { EmpresaService } from 'src/app/services/empresa.service';
import { UtilsService } from 'src/app/services/utils.service';
import { Camera, CameraResultType } from '@capacitor/camera';

enum WHICH_DATE {
  MIN,
  MAX,
  NOW
}

@Component({
  selector: 'app-actos-condiciones-inseguras',
  templateUrl: './actos-condiciones-inseguras.page.html',
  styleUrls: ['./actos-condiciones-inseguras.page.scss'],
})
export class ActosCondicionesInsegurasPage implements OnInit {

  sedes: Sede[];

  procesos: Proceso[];

  tipos: Tipo[];

  fechaOcurrencia: string;

  imageSelected: boolean;

  WHICH_DATE_ENUM = WHICH_DATE;

  formGroup: FormGroup;

  compareEntities: any;

  opcion: string;

  readonly MONTH_SHORT_NAMES = MONTHS_SHORT;

  private submitAttempt: boolean;

  private actoCondicionInsegura: ActoCondicionInsegura;

  private sedesSubscription: Subscription;

  private procesosSubscription: Subscription;

  private tipoSubscription: Subscription;
  base64Img: any;

  constructor(
    private formBuilder: FormBuilder,
    private actionSheetCtrl: ActionSheetController,
    private context: ContextService,
    private empresaService: EmpresaService,
    private cameraService: CameraService,
    private actoCondicionInseguraService: ActoCondicionInseguraService,
    private alertCtrl: AlertController,
    private utils: UtilsService
  ) {
    this.imageSelected = false;
    this.submitAttempt = false;
    this.fechaOcurrencia = (new Date()).toISOString();
    this.actoCondicionInsegura = new ActoCondicionInsegura();
    this.compareEntities = Entity.compare;

    this.formGroup = formBuilder.group({
      sedeFormControl: ['', Validators.compose([Validators.required])],
      tipoFormControl: ['', Validators.compose([Validators.required])],
      procesoFormControl: ['', Validators.compose([])],
      lugarFormControl: ['', Validators.compose([Validators.required, Validators.maxLength(100)])],
      fechaFormControl: ['', Validators.compose([Validators.required])],
      descripcionFormControl: ['', Validators.compose([Validators.required, Validators.maxLength(1000)])],
      causaFormControl: ['', Validators.compose([Validators.required, Validators.maxLength(1000)])],
      sugerenciaFormControl: ['', Validators.compose([Validators.required, Validators.maxLength(1000)])]
    });
  }

  ngOnInit(): void {
    const getData = async (nit: string): Promise<void> => {
      await this.utils.presentLoading();

      this.sedesSubscription = this.empresaService.getSedes(nit).subscribe(
        (response: any) => {
          this.sedes = Sede.parse(response);
          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );

      await this.utils.presentLoading();

      this.procesosSubscription = this.empresaService.getProcesos(nit).subscribe(
        (response: any) => {
          this.procesos = Proceso.parse(response);
          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );

      await this.utils.presentLoading();

      this.tipoSubscription = this.actoCondicionInseguraService.getTipos().subscribe(
        (response: any) => {
          this.tipos = Tipo.parse(response);
          this.utils.dismissLoading();
        },
        (error: any) => {
          alertCommunicationError(this.alertCtrl);
          this.utils.dismissLoading();
        }
      );
    };

    this.context.get().then(c => {
      const nit: string = c.empresa.id.toString();
      getData(nit);
    });
  }

  ionViewWillEnter(): void {
    this.opcion = 'unsafeConditions';
  }

  ionViewDidLeave(): void {
    this.opcion = undefined;
  }

  isSedeInvalid(): boolean {
    return !this.formGroup.controls.sedeFormControl.valid
      && this.submitAttempt;
  }

  isTipoInvalid(): boolean {
    return !this.formGroup.controls.tipoFormControl.valid
      && this.submitAttempt;
  }

  isProcesoInvalid(): boolean {
    return !this.formGroup.controls.procesoFormControl.valid
      && this.submitAttempt;
  }

  isLugarInvalid(): boolean {
    return !this.formGroup.controls.lugarFormControl.valid
      && this.submitAttempt;
  }

  isFechaInvalid(): boolean {
    return !this.formGroup.controls.fechaFormControl.valid
      && this.submitAttempt;
  }

  isDescripcionInvalid(): boolean {
    return !this.formGroup.controls.descripcionFormControl.valid
      && this.submitAttempt;
  }

  isCausaInvalid(): boolean {
    return !this.formGroup.controls.causaFormControl.valid
      && this.submitAttempt;
  }

  isSugerenciaInvalid(): boolean {
    return !this.formGroup.controls.sugerenciaFormControl.valid
      && this.submitAttempt;
  }

  async presentCameraGalleryActionSheet(): Promise<void> {
    this.takePicture();
    /*
    const actionSheet = await this.actionSheetCtrl.create({
      header: 'Seleccione una Opción',
      backdropDismiss: true,
      buttons: [
        {
          text: 'Usar Cámara',
          icon: '',
          //handler: () => this.getPicture(PictureSourceType.CAMERA)
          handler: () => this.takePicture()
        },
        {
          text: 'Cargar Desde Galería',
          icon: '',
          handler: () => this.getPicture(PictureSourceType.PHOTOLIBRARY)
        }
      ]
    });
    await actionSheet.present();
    */
  }

  getPicture(pictureSourceType: PictureSourceType): void {
    this.cameraService.getPicture(pictureSourceType)
      .then((imgData) => {
        this.actoCondicionInsegura.evidenciaImagePath = imgData;
        this.imageSelected = true;
      })
      .catch((err) => this.imageSelected = false);
  }

  async reportar(): Promise<void> {
    this.submitAttempt = true;
    if (!this.formGroup.valid) {
      this.alert('Alerta', '', 'Los campos señalados son requeridos.');
      return;
    }

    //  Evidencia obligatoria
    
    if (this.imageSelected == false) {

      this.alert('Alerta', '', 'La evidencia es obligatoria para generar el reporte.');
      return;

    }

    await this.utils.presentLoading();
    await this.getDataFromInputs();
    this.actoCondicionInsegura.imagen = this.base64Img;
    console.log('Reportar: ' + JSON.stringify(this.actoCondicionInsegura));
    this.actoCondicionInseguraService.post(this.actoCondicionInsegura).subscribe(
      (result: any) => {
        this.alert('Confirmación', '', 'Se ha generado el reporte exitosamente.');
        this.submitAttempt = false;
        this.formGroup.markAsPristine();
        this.cleanInputs();
        this.utils.dismissLoading();
      },
      (error: any) => {
        alertCommunicationError(this.alertCtrl);
        this.utils.dismissLoading();
      }
    );
  }

  getDate(whichDate: WHICH_DATE): string {
    const date = new Date();
    switch (whichDate) {
      case WHICH_DATE.MIN:
        return '' + (date.getFullYear() - 1);
      case WHICH_DATE.MAX:
      case WHICH_DATE.NOW:
        return date.getFullYear()
          + '-' + ('0' + (date.getMonth() + 1)).slice(-2)
          + '-' + ('0' + date.getDate()).slice(-2);
    }
  }

  async alert(title: string, subTitle: string, message: string): Promise<HTMLIonAlertElement> {
    const alert = await this.alertCtrl.create({
      header: title,
      subHeader: subTitle,
      message,
      buttons: [
        { text: 'Aceptar', role: 'null' }
      ],
      backdropDismiss: true
    });

    await alert.present();

    return alert;
  }

  async getDataFromInputs(): Promise<void> {
    const c = await this.context.get();
    this.actoCondicionInsegura.idEmpresa = c.empresa.id;
    this.actoCondicionInsegura.idTrabajador = c.trabajador.id;
    this.actoCondicionInsegura.causa = this.formGroup.controls.causaFormControl.value;
    this.actoCondicionInsegura.descripcion = this.formGroup.controls.descripcionFormControl.value;
    const fecha = this.fechaOcurrencia.split('T')[0].split('-');
    this.actoCondicionInsegura.fecha = new Date(parseInt(fecha[0], 10), parseInt(fecha[1], 10) - 1, parseInt(fecha[2], 10));
    this.actoCondicionInsegura.lugar = this.formGroup.controls.lugarFormControl.value;
    this.actoCondicionInsegura.proceso = this.formGroup.controls.procesoFormControl.value;
    this.actoCondicionInsegura.sede = this.formGroup.controls.sedeFormControl.value;
    this.actoCondicionInsegura.sugerencia = this.formGroup.controls.sugerenciaFormControl.value;
    this.actoCondicionInsegura.tipo = this.formGroup.controls.tipoFormControl.value;
  }

  cleanInputs(): void {
    this.formGroup.controls.causaFormControl.setValue('');
    this.formGroup.controls.descripcionFormControl.setValue('');
    this.formGroup.controls.fechaFormControl.setValue('');
    this.formGroup.controls.lugarFormControl.setValue('');
    this.formGroup.controls.procesoFormControl.setValue('');
    this.formGroup.controls.sedeFormControl.setValue('');
    this.formGroup.controls.sugerenciaFormControl.setValue('');
    this.formGroup.controls.tipoFormControl.setValue('');
    this.imageSelected = false;
  }

  async takePicture() {
    try {
        const image = await Camera.getPhoto({
          quality: 90,
          allowEditing: false,
          resultType: CameraResultType.Base64
        });
        this.base64Img = image.base64String;
        this.imageSelected = true;
        console.log('Evidencia: ' + JSON.stringify(this.base64Img));
    } catch (error) {
        console.error('Error al capturar la foto:', error);
    }
  }

}
