import { Proceso } from './proceso';
import { Sede } from './sede';
import { Tipo } from './tipo';

export class ActoCondicionInsegura {
  get idTrabajador(): number {
    return this._idTrabajador;
  }

  set idTrabajador(idTrabajador: number) {
    this._idTrabajador = idTrabajador;
  }

  get idEmpresa(): number {
    return this._idEmpresa;
  }

  set idEmpresa(idEmpresa: number) {
    this._idEmpresa = idEmpresa;
  }

  get sede(): Sede {
    return this._sede;
  }

  set sede(sede: Sede) {
    this._sede = sede;
  }

  get tipo(): Tipo {
    return this._tipo;
  }

  set tipo(tipo: Tipo) {
    this._tipo = tipo;
  }

  get proceso(): Proceso {
    return this._proceso;
  }

  set proceso(proceso: Proceso) {
    this._proceso = proceso;
  }

  get lugar(): string {
    return this._lugar;
  }

  set lugar(lugar: string) {
    this._lugar = lugar;
  }

  get fecha(): Date {
    return this._fecha;
  }

  set fecha(fecha: Date) {
    this._fecha = fecha;
  }

  get descripcion(): string {
    return this._descripcion;
  }

  set descripcion(descripcion: string) {
    this._descripcion = descripcion;
  }

  get causa(): string {
    return this._causa;
  }

  set causa(causa: string) {
    this._causa = causa;
  }

  get sugerencia(): string {
    return this._sugerencia;
  }

  set sugerencia(sugerencia: string) {
    this._sugerencia = sugerencia;
  }

  get evidenciaImagePath(): string {
    return this._evidenciaImagePath;
  }

  set evidenciaImagePath(evidenciaImagePath: string) {
    this._evidenciaImagePath = evidenciaImagePath;
  }

  set imagen(imagen: string) {
    this._imagen = imagen;
  }

  private _idTrabajador: number;

  private _idEmpresa: number;

  private _sede: Sede;

  private _tipo: Tipo;

  private _proceso: Proceso;

  private _lugar: string;

  private _fecha: Date;

  private _descripcion: string;

  private _causa: string;

  private _sugerencia: string;

  private _evidenciaImagePath: string;

  private _imagen: string;

  constructor() {}

  toUrlSearchParams(): URLSearchParams {
    const params: URLSearchParams = new URLSearchParams();
    params.append('idSede', String(this._sede.id));
    params.append('idTipo', String(this._tipo.id));
    params.append('idProceso', String(this._proceso.id));
    params.append('lugar', this._lugar);
    params.append('descripcion', this._descripcion);
    params.append('causa', this._causa);
    params.append('sugerencia', this._sugerencia);
    params.append('evidencia', this._evidenciaImagePath);
    return params;
  }

  toJson(): string {
    return JSON.stringify({
      nitEmpresa: String(this.idEmpresa),
      cedulaQuienReporta: String(this.idTrabajador),
      sede: String(this._sede.id),
      tipo: String(this._tipo.id),
      proceso: this._proceso.id === undefined ? undefined : String(this._proceso.id),
      area: this._lugar,
      fechaOcurrencia: this._fecha.getFullYear() + '/' + (this._fecha.getMonth() + 1) + '/' + this._fecha.getDate(),
      descripcion: this._descripcion,
      causa: this._causa,
      sugerencia: this._sugerencia,
      imagen: this._imagen,
    });
  }
}
